import { copyArr, copyArr2D, zero, scaleArray, addArrays, last} from './utils'

export function pivot(tableau, baseVariables, i, j) {
  const newTableau = copyArr2D(tableau);
  newTableau[i] = scaleArray(newTableau[i], newTableau[i][j].inverse());
  for (let k = 0; k < newTableau.length; k ++) {
    if (k !== i) {
      newTableau[k] = addArrays(newTableau[k], scaleArray(newTableau[i], newTableau[k][j].neg()));
    }
  }

  const newBaseVariables = copyArr(baseVariables);
  newBaseVariables[i - 1] = j;
  
  return [newTableau, newBaseVariables];
}

export function nonePivotSuggestion(tableau) {
  return { pivots: tableau.map(row => row.map(() => false)), description: "None"};
}

export function emptyPrimalPivots(tableau) {
  return {
    "generic_primal": { pivots: tableau.map(row => row.map(() => false)), 
                        description: "Generic pivot for primal simplex" },
    "bland_primal"  : { pivots: tableau.map(row => row.map(() => false)), 
                        description: "Bland's rule for primal simplex" },
  }
}

export function emptyDualPivots(tableau) {
  return {
    "generic_dual": { pivots: tableau.map(row => row.map(() => false)), 
                      description: "Generic pivot for dual simplex" },
    "bland_dual"  : { pivots: tableau.map(row => row.map(() => false)), 
                      description: "Bland's rule for dual simplex" },
  };
}

export function calculatePrimalPivots(tableau, baseColumns, disallowSlack=false, n=null) {
  const results = emptyPrimalPivots(tableau);

  if (!tableau.slice(1).every(row => last(row).compare(zero) >= 0)) 
    return [results, false];

  let foundBland = false;
  let unbounded = false;
  for (let j = 0; j < tableau[0].length - 1; j ++) {
    if (baseColumns[j] || tableau[0][j].compare(zero) >= 0) continue;
    let minDelta = null;
    let maxI = null;
    for (let i = 1; i < tableau.length; i ++) {
      if (tableau[i][j].compare(zero) <= 0) continue;
      if (minDelta === null || minDelta.compare(last(tableau[i]).div(tableau[i][j])) > 0) {
        minDelta = last(tableau[i]).div(tableau[i][j]);
        maxI = i;
      }
    }
    if (minDelta === null) {
      unbounded = true;
      continue;
    }
    for (let i = 1; i < tableau.length; i ++) {
      if (tableau[i][j].compare(zero) <= 0) continue;
      if (minDelta.equals(last(tableau[i]).div(tableau[i][j]))) {
        results["generic_primal"].pivots[i][j] = true;
      }
    }
    if (!foundBland) {
      foundBland = true;
      results["bland_primal"].pivots[maxI][j] = true;
    }
  }
  if (disallowSlack) {
    results["generic_primal"].pivots = results["generic_primal"].pivots.map(row => row.map((v, j) => j < n && v));
    results["bland_primal"].pivots = results["bland_primal"].pivots.map(row => row.map((v, j) => j < n && v));
  }
  return [results, unbounded];
}

export function calculateDualPivots(tableau, disallowSlack=false, n=null) {
  const results = emptyDualPivots(tableau);
  if (!tableau[0].slice(0, -1).every(x => x.compare(zero) >= 0))
    return [results, false];

  let foundBland = false;
  let unbounded = false;

  for (let i = 1; i < tableau.length; i ++) {
    if (last(tableau[i]).compare(0) >= 0) continue;
    let minRatio = null;
    let minJ = null;
    for (let j = 0; j < tableau[i].length - 1; j ++) {
      if (tableau[i][j].compare(zero) >= 0) continue;
      const ratio = tableau[0][j].div(tableau[i][j]).abs();
      if (minRatio === null || ratio.compare(minRatio) < 0) {
        minRatio = ratio;
        minJ = j;
      }
    }
    if (minRatio === null) {
      unbounded = true;
      continue;
    }
    for (let j = 0; j < tableau[i].length - 1; j ++) {
      if (tableau[i][j].compare(zero) >= 0) continue;
      if (minRatio.equals(tableau[0][j].div(tableau[i][j]).abs())) {
        results["generic_dual"].pivots[i][j] = true;
      }
    }
    if (!foundBland) {
      foundBland = true;
      results["bland_dual"].pivots[i][minJ] = true;
    }
  }
  if (disallowSlack) {
    results["generic_dual"].pivots = results["generic_dual"].pivots.map(row => row.map((v, j) => j < n && v));
    results["bland_dual"].pivots = results["bland_dual"].pivots.map(row => row.map((v, j) => j < n && v));
  }
  return [results, unbounded];
}