import { InlineMath } from 'react-katex';
import { range, zero, one, minusOne, toLatex } from './utils'
import './Program.scss'

function Monomial({
  value, 
  label, 
  first = false, 
  firstNonZero = false, 
  lastInEmptyRow = false,
  useDecimal = true,
  useMixed = true,
}) {
  return first ? (
    <td>
      <InlineMath>{
        lastInEmptyRow         ? "0"         :
        value.equals(zero)     ? ""          : 
        value.equals(one)      ? label       :
        value.equals(minusOne) ? `-${label}` :
        `${toLatex(value.abs(), useMixed, useDecimal)} ${label}`
      }</InlineMath>
    </td>
  ) : (
    <>
      <td className="operator">
        <InlineMath>{
          value.equals(zero)      ? ""  : 
          value.compare(zero) < 0 ? "-" :
          !firstNonZero           ? "+" : ""
        }</InlineMath>
      </td>
      <td>
        <InlineMath>{
          lastInEmptyRow          ? "0"   :
          value.equals(zero)      ? ""    :
          value.abs().equals(one) ? label : 
          `${toLatex(value.abs(), useMixed, useDecimal)} ${label}`
        }</InlineMath>
      </td>
    </>
  );
}

export function ProgramDisplay({program, useDecimal, useMixed}) {
  // console.log(program);
  function getFirstNonZeroIndex(arr) {
    return arr.map(v => !v.equals(zero)).indexOf(true);
  }
  const isDual = program.isDual()
  const showPrimal = !isDual;
  const isEquality = program.isEquality();
  const firstNonZero = showPrimal ?
    range(program.m).map(i => getFirstNonZeroIndex(program.A[i])) :
    range(program.n).map(j => 
      getFirstNonZeroIndex(range(program.m).map(i => program.A[i][j])));
  const firstNonZeroInTopRow = getFirstNonZeroIndex(showPrimal ? program.c : program.b);
  return (
    <table className="program-display">
      <tbody>
        <tr>
          <td><InlineMath>{showPrimal ? "max" : "min"}</InlineMath></td>
          {range(showPrimal ? program.n : program.m).map(i =>
            <Monomial
              key={i}
              value={showPrimal ? program.c[i] : program.b[i]}
              first={i === 0}
              label={program.labels[i]}
              firstNonZero={i === firstNonZeroInTopRow}
              lastInEmptyRow={i === (showPrimal ? program.n : program.m) - 1 
                              && firstNonZeroInTopRow === -1}
              useDecimal={useDecimal}
              useMixed={useMixed}
            />
          )}
        </tr>
        {range(showPrimal ? program.m : program.n).map(i =>
          <tr key={i}>
            <td></td>
            {range(showPrimal ? program.n : program.m).map(j =>
              <Monomial
                key={j}
                value={showPrimal ? program.A[i][j] : program.A[j][i]}
                first={j === 0}
                label={program.labels[j]}
                firstNonZero={j === firstNonZero[i]}
                lastInEmptyRow={j === (showPrimal ? program.n : program.m) - 1 
                                && firstNonZero[i] === -1}
                useDecimal={useDecimal}
                useMixed={useMixed}
              />
            )}
            <td><InlineMath>{isDual ? "\\geq" : isEquality ? "=" : "\\geq"}</InlineMath></td>
            <td>
              <InlineMath>
                {toLatex(showPrimal ? program.b[i] : program.c[i], useMixed, useDecimal)}
              </InlineMath>
            </td>
          </tr>
        )}
        {program.form === "equality_dual" ? null :
        <tr>
          <td colSpan={showPrimal ? 2 * program.n : 2 * program.m}>
            <InlineMath>
              {program.labels.join(", ")}
            </InlineMath>
          </td>
          <td><InlineMath>\geq</InlineMath></td>
          <td><InlineMath>0</InlineMath></td>
        </tr>}
      </tbody>
    </table>
  );
}

export function SlackForm({tableau, useDecimal, useMixed}) {
  const nonBasicColumns = range(tableau.columns - 1).filter(i => !tableau.isBase[i]);
  return (
    <table className="program-display">
      <tbody>
        <tr>
          <td><InlineMath>z</InlineMath></td>
          <td><InlineMath>=</InlineMath></td>
          <td><InlineMath>{toLatex(tableau.tableau[0][tableau.columns-1], useMixed, useDecimal)}</InlineMath></td>
          {nonBasicColumns.map(i =>
            <Monomial
              key={i}
              value={tableau.tableau[0][i].neg()}
              label={tableau.primalLabels[i]}
              useDecimal={useDecimal}
              useMixed={useMixed}
            />
          )}
        </tr>
        {range(tableau.m).map(i =>
          <tr key={i}>
            <td><InlineMath>{tableau.baseVariables[i] >= 0 ? tableau.primalLabels[tableau.baseVariables[i]] : "0"}</InlineMath></td>
            <td><InlineMath>=</InlineMath></td>
            <td><InlineMath>{tableau.tableau[i+1][tableau.columns-1].toLatex()}</InlineMath></td>
            {nonBasicColumns.map(j =>
              <Monomial
                key={j}
                value={tableau.tableau[i+1][j].neg()}
                label={tableau.primalLabels[j]}
                useDecimal={useDecimal}
                useMixed={useMixed}
              />
            )}
          </tr>
        )}
      </tbody>
    </table>
  )
}