import React, { useState } from 'react';
import { InlineMath } from 'react-katex'
import Switch from "react-switch";
import { Pane, MultiPane, SplitPane } from './Layout'
import { range, copyArr, toLatex, flipSelection, isAllowedToSelect } from './utils'
import { getStateFromProgram } from './SimplexState'
import { ProgramDisplay, SlackForm } from './ProgramDisplay'
import { TableauDisplay } from './TableauDisplay'
import './Tableau.scss'
import './Input.scss'

function SolutionDisplay({
  title,
  labels, 
  solution, 
  toShow = solution.length, 
  feasible, 
  useDecimal = true, 
  useMixed = false,
}) {
  return (
    <div className="solution">
      <div className="solution-header">{title} 
        {feasible ?
          <span colSpan={toShow} className="highlight-pos">Feasible</span> :
          <span colSpan={toShow} className="highlight-neg">Infeasible</span>
        }
      </div>
      <div className="inner">
        <table className="solution-table">
          <tbody>
            <tr>
              {range(toShow).map(i => 
                <td key={i}><InlineMath>{labels[i]}</InlineMath></td>
              )}
            </tr>
            <tr>
              {range(toShow).map(i => 
                <td key={i}><InlineMath>{toLatex(solution[i], useMixed, useDecimal)}</InlineMath></td>
              )}
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  )
}

function SelectSetting({name, description, currentValue, options, onChange}) {
  return (
    <div className="setting">
      <select 
        className="setting-select"
        name={name}
        value={currentValue}
        onChange={e => onChange(e.target.value)}
      >
        {options.map((option, i) =>
          <option 
            key={i}
            value={option.value}
          >
            {option.label}
          </option>
        )}
      </select>
    </div>
  );
}

function SwitchSetting({name, description, currentValue, onChange, color}) {
  return (
    <div className="setting">
      <div className="switch">
        <Switch 
          id={name}
          height={25}
          width={50}
          onChange={val => onChange(val)} 
          onColor={color}
          checked={currentValue}
        />
      </div>
      <label 
        htmlFor={name}
        className="description"
      >
        {description}
      </label>
    </div>
  );
}

function Settings({settings, setSetting, availablePivots}) {
  return (
    <div id="settings">
      <div className="setting-pivot">
        <div className="header">Suggested Pivots</div>
        <SelectSetting 
          name="highlighted_pivot"
          description="Suggested"
          currentValue={settings.highlightedPivots}
          options={Object.entries(availablePivots).map(s => ({ value: s[0], label: s[1].description }))}
          onChange={newValue => setSetting("highlightedPivots", newValue)}
        />
      </div>
      <div className="setting-base">
        <div className="header">Base columns</div>
        <SwitchSetting 
          name="highlight_base"
          color="#88c5ff"
          description="Highlight base columns"
          currentValue={settings.highlightBase}
          onChange={newValue => setSetting("highlightBase", newValue)}
        />
        <SwitchSetting 
          name="show_base"
          color="#88c5ff"
          description="Show base columns"
          currentValue={settings.showBase}
          onChange={newValue => setSetting("showBase", newValue)}
        />
      </div>
      <div className="setting-other">
        <div className="header">Other settings</div>
        <SwitchSetting 
          name="highlight_invariants"
          color="#6cd875"
          description="Highlight invariants"
          currentValue={settings.highlightInvariants}
          onChange={newValue => setSetting("highlightInvariants", newValue)}
        />
        <SwitchSetting 
          name="use_decimal"
          color="#6cd875"
          description="Use decimal fractions"
          currentValue={settings.useDecimal}
          onChange={newValue => setSetting("useDecimal", newValue)}
        />
        <SwitchSetting 
          name="use_mixed"
          color="#6cd875"
          description="Use mixed fractions"
          currentValue={settings.useMixed}
          onChange={newValue => setSetting("useMixed", newValue)}
        />
        <SwitchSetting 
          name="show_disabled"
          color="#6cd875"
          description="Darken cells where pivot cannot be done"
          currentValue={settings.showDisabled}
          onChange={newValue => setSetting("showDisabled", newValue)}
        />
        <SwitchSetting 
          name="objective_at_bottom"
          color="#6cd875"
          description="Display objective function row at the bottom"
          currentValue={settings.showObjectiveBottom}
          onChange={newValue => setSetting("showObjectiveBottom", newValue)}
        />
        <SwitchSetting 
          name="b_column_on_left"
          color="#6cd875"
          description="Display the b column on the left"
          currentValue={settings.showBColumnsLeft}
          onChange={newValue => setSetting("showBColumnsLeft", newValue)}
        />
        <SwitchSetting 
          name="slack_on_left"
          color="#6cd875"
          description="Display the slack variables on the left"
          currentValue={settings.showSlackLeft}
          onChange={newValue => setSetting("showSlackLeft", newValue)}
        />
      </div>
    </div>
  );
}

function InteractiveTableau({initialProgram, onStartOver}) {
  const [state, setState] = useState({
    stateHistory: [getStateFromProgram(initialProgram)],
    index: 0,
    settings: {
      highlightedPivots: "bland_primal",
      highlightBase: true,
      showBase: true,
      highlightInvariants: true,
      useDecimal: true,
      useMixed: false,
      showDisabled: true,
      showObjectiveBottom: false,
      showBColumnsLeft: false,
      showSlackLeft: false,
    },
    selectedProgram: null,
    currentTransition: null
  });

  const currentTableau = state.stateHistory[state.index]; 
  let highlightedPivots = state.settings.highlightedPivots;
  if (!(highlightedPivots in currentTableau.suggestedPivots)) {
    highlightedPivots = currentTableau.defaultSuggestion;
    setSetting("highlightedPivots", currentTableau.defaultSuggestion);
  }

  // console.log(state);
  // console.log(currentTableau);

  function goToPrevious() { setState({ ...state, index: state.index - 1, currentTransition: null }); }
  function goToNext()     { setState({ ...state, index: state.index + 1, currentTransition: null }); }

  function appendTableau(newTab) {
    setState({
      ...state,
      stateHistory: state.stateHistory.slice(0, state.index + 1).concat(newTab),
      index: state.index + 1,
      currentTransition: null
    })
  }

  function handleCellClick(i, j) {
    if (state.currentTransition === null) {
      if (currentTableau.canPivot(i, j))
        appendTableau(currentTableau.pivot(i, j));  
    } else {
      if (isAllowedToSelect(i, j, state.currentTransition.selection)) {
        setState({
          ...state,
          currentTransition: {
            ...state.currentTransition,
            selection: flipSelection(i, j, state.currentTransition.selection)
          }
        });
      }
    }
  }

  function makeTransition(transition) {
    if (transition.args === "none")
      appendTableau(transition.getNewState());
    else {
      setState({
        ...state,
        currentTransition: {
          getNewState: transition.getNewState,
          validateArgs: transition.validateArgs,
          prompt: transition.prompt,
          selection: {
            type: transition.args,
            selected: copyArr(transition.defaultSelection),
            allowed: transition.allowed,
          },
          errorMessage: null
        }
      });
    }
  }

  function finishTransition() {
    const args = state.currentTransition.selection.selected;
    const [correct, error] = state.currentTransition.validateArgs(args);
    if (correct) {
      appendTableau(state.currentTransition.getNewState(args));
    } else {
      setState({
        ...state,
        currentTransition: {
          ...state.currentTransition,
          errorMessage: error
        }
      })
    }
  }

  function cancelTransition() {
    setState({
      ...state,
      currentTransition: null,
    })
  }

  function selectAll() {
    setState({
      ...state,
      currentTransition: {
        ...state.currentTransition,
        selection: {
          ...state.currentTransition.selection,
          selected: copyArr(state.currentTransition.selection.allowed)
        }
      }
    })
  }

  function setSetting(setting, value) {
    setState({
      ...state,
      settings: {
        ...state.settings,
        [setting]: value
      }
    })
  }

  return (
    <div id="inner-wrapper">
      <div id="tab-and-controls">
        <div id="controls">
          <button
            onClick={goToPrevious}
            disabled={state.index === 0}
          >
            Previous
          </button>
          <span id="counter">{`${state.index+1} / ${state.stateHistory.length}`}</span>
          <button
            onClick={goToNext}
            disabled={state.index === state.stateHistory.length - 1}
          >
            Next
          </button>
        </div>
        <TableauDisplay
          state={currentTableau}
          onCellClick={handleCellClick}
          showDisabled={true}
          selection={state.currentTransition === null ? null : state.currentTransition.selection}
          {...state.settings}
          highlightedPivots={highlightedPivots}
        />
        <div id="message">
          {state.currentTransition === null ?
          (<>
            <p>{currentTableau.getStatusMessage().split("$").map((subMsg, i) => 
                i % 2 === 0 ? subMsg : (<InlineMath>{subMsg}</InlineMath>))}</p>
            {currentTableau.transitions.map((transition, i) => 
              <button 
                key={i} 
                className="inline-button"
                onClick={() => makeTransition(transition)}>
                  {transition.label}
              </button>
            )}
          </>) :
          (<>
            {state.currentTransition.prompt}
            <button onClick={finishTransition} className="inline-button">Done</button>
            <button onClick={selectAll} className="inline-button">Select All</button>
            <button onClick={cancelTransition} className="inline-button">Cancel</button>
            {state.currentTransition.errorMessage === null ? null :
              <p className="highlight-neg">{state.currentTransition.errorMessage}</p>
            }
          </>)
          }
        </div>
      </div>
      <div id="sidebar">
        <Settings
          settings={{...state.settings, highlightedPivots: highlightedPivots}}
          availablePivots={currentTableau.suggestedPivots}
          setSetting={setSetting}
        />
        <button onClick={onStartOver} id="start-over">Start over with a new program</button>
      </div>
      <SplitPane>
        <SolutionDisplay
          title={"Primal solution"}
          labels={currentTableau.primalLabels}
          solution={currentTableau.primalSolution}
          feasible={currentTableau.primalFeasible}
          {...state.settings}
        />
        <SolutionDisplay
          title={"Dual solution"}
          labels={currentTableau.dualLabels}
          solution={currentTableau.dualSolution}
          feasible={currentTableau.dualFeasible}
          {...state.settings}
        />
      </SplitPane>
      <MultiPane
        defaultChoice={currentTableau.defaultProgram}
        chosen={state.selectedProgram}
        setChosen={choice => setState({...state, selectedProgram: choice})}
      >
        {currentTableau.programs.map(({name, program}, i) =>
          <Pane 
            key={i}
            name={name}
            label={name}
          >
            <ProgramDisplay
              program={program}
              {...state.settings}
            />
          </Pane>
        )}
        {currentTableau.isEquality ? null :
        <Pane
          name="slack"
          label="Slack Form"
        >
          <SlackForm
            tableau={currentTableau}
            {...state.settings}
          />
        </Pane>}
      </MultiPane>
    </div>
  );
}

export { InteractiveTableau };