import { range, copyArr, copyArr2D, zero, last, cutToLength, isIntegral, isIntegral2D, updateArr2D, updateArr, listToBoolArray, inverseArray } from './utils'
import { pivot } from './pivots'
// import Fraction from 'fraction.js/bigfraction'
import Fraction from 'fraction.js'

const getZeroFraction = () => zero;
const getZeroRow = len => () => range(len).map(() => zero);

export default class Program {
  constructor({n, m, A, b, c, form, labels=null}) {
    this.n = n;
    this.m = m;
    this.A = A;
    this.b = b;
    this.c = c;  
    this.form = form;
    this.labels = labels !== null ? labels :
      form === 'primal' || form === "equality" ? 
        range(n).map(i => `x_{${i + 1}}`) : range(m).map(i => `y_{${i + 1}}`);
  }

  static zeroProgram(n, m) {
    return new Program({n: n, m: m, 
      A: range(m).map(() => range(n).map(() => zero)),
      b: range(m).map(() => zero),
      c: range(n).map(() => zero),
      form: 'primal',
    });
  }

  static fromArrays(A, b, c, form='primal', labels=null) {
    return new Program({n: c.length, m: b.length, 
      A: A.map(r => r.map(v => Fraction(v))),
      b: b.map(v => Fraction(v)),
      c: c.map(v => Fraction(v)),
      form: form,
      labels: labels
    });
  }

  static fromTableau(n, m, tableau, baseVariables, labels) {
    let isBase = listToBoolArray(baseVariables, n + m);

    for (let j = 0; j < m; j ++) {
      if (isBase[n + j]) continue;

      for (let i = 1; i <= m; i ++) {
        if (!tableau[i][n + j].equals(zero) && baseVariables[i-1] < n) {
          [tableau, baseVariables] = pivot(tableau, baseVariables, i, n + j);
          isBase = listToBoolArray(baseVariables, n + m);
          break;
        }
      }
    }

    const invBaseVariables = inverseArray(baseVariables, n + m);
    tableau = [tableau[0]].concat(range(m).map(j => tableau[invBaseVariables[n + j]+1]));

    return new Program({
      n: n, m: m,
      A: range(m).map(i => range(n).map(j => tableau[i+1][j])),
      b: range(m).map(i => last(tableau[i+1])),
      c: range(n).map(j => tableau[0][j].neg()),
      form: 'primal',
      labels: labels.slice(0, n),
    })
  }

  isDual() {
    return this.form === 'dual' || this.form === 'equality_dual';
  }

  isEquality() {
    return this.form === 'equality' || this.form === 'equality_dual';
  }

  copy() {
    return new Program({
      ...this,
      A: copyArr2D(this.A),
      b: copyArr(this.b),
      c: copyArr(this.c),
    });
  }

  updateSize(n, m) {
    const Acopy = cutToLength(
        this.A.map(row => cutToLength(row, n, getZeroFraction)), 
        m, getZeroRow(n));
    const bcopy = cutToLength(this.b, m, getZeroFraction);
    const ccopy = cutToLength(this.c, n, getZeroFraction);

    return new Program({n: n, m: m, A: Acopy, b: bcopy, c: ccopy, form: this.form});
  }

  updateA(i, j, val) {
    return new Program({
      ...this,
      A: updateArr2D(this.A, i, j, val)
    })
  }

  updateB(i, val) {
    return new Program({
      ...this,
      b: updateArr(this.b, i, val)
    })
  }

  updateC(j, val) {
    return new Program({
      ...this,
      c: updateArr(this.c, j, val)
    })
  }

  setForm(f) {
    return new Program({
      ...this,
      form: f,
      labels: null
    });
  }

  setLabels(labels) {
    return new Program({
      ...this,
      labels: labels
    })
  }

  isIntegral() {
    return isIntegral2D(this.A) && isIntegral(this.b) && isIntegral(this.c);
  }
}