import Program from './Program'

const examples = [
  {
    name: "Simple 1",
    program: Program.fromArrays(
      [
        [1, 1, 3],
        [2, 2, 5],
        [4, 1, 2]
      ],
      [30, 24, 36],
      [3, 1, 2],
      "primal"
    )
  },
  {
    name: "Simple 2",
    program: Program.fromArrays(
      [
        [1, 1],
        [1, 0],
        [0, 1]
      ],
      [20, 12, 16],
      [18, 12.5],
      "primal"
    )
  },
  {
    name: "Basic solution not feasible",
    program: Program.fromArrays(
      [
        [2, -1],
        [1, -5]
      ],
      [2, -4],
      [2, -1],
      "primal"
    )
  },
  {
    name: "Basic solution not feasible 2",
    program: Program.fromArrays(
      [
        [-2, "-7.5", 3],
        [-20, -5, -3],
      ],
      [-10000, -30000],
      [-1, -1, -1],
      "primal"
    )
  },
  {
    name: "Infeasible",
    program: Program.fromArrays(
      [
        [-1, -1, 0],
        [0, -1, -1],
        [1, 2, 1],
      ],
      [-2, -3, 4],
      [1, 1, 1],
      "primal"
    )
  },
  {
    name: "Unbounded",
    program: Program.fromArrays(
      [
        [1, -1, 1],
        [-2, 1, 0],
        [0, 1, -2]
      ],
      [5, 3, 5],
      [0, 2, 1],
      "primal"
    )
  },
  {
    name: "Dual unbounded",
    program: Program.fromArrays(
      [
        [1, 0],
        [3, 2],
      ],
      [1, -1],
      [-2, 0],
      "dual"
    )
  },
  {
    name: "Gomory cuts",
    program: Program.fromArrays(
      [
        [-1, 1],
        [8, 2],
      ],
      [2, 17],
      ["5.5", "2.1"],
      "primal"
    )
  },
  {
    name: "Equality Form",
    program: Program.fromArrays(
      [
        [1, 1, 1],
        [2, 2, 5],
      ],
      [5, 16],
      [3, 1, 2],
      "equality"
    )
  },
  {
    name: "Equality Form 2",
    program: Program.fromArrays(
      [
        [-1, -1, -1],
        [2, 2, 3],
      ],
      [-8, 16],
      [3, 1, 2],
      "equality"
    )
  },
  {
    name: "Equality infeasible",
    program: Program.fromArrays(
      [
        [1, 1, 1],
        [2, 2, 5],
      ],
      [12, 16],
      [3, 1, 2],
      "equality"
    )
  },
  {
    name: "Equality redundant row",
    program: Program.fromArrays(
      [
        [1, 1],
        [2, 3],
        [3, 4],
      ],
      [4, 9, 13],
      [1, 1],
      "equality"
    )
  },
  {
    name: "Equality redundant row 2",
    program: Program.fromArrays(
      [
        [1, 1, 1, 3],
        [2, 3, 1, 2],
        [3, 4, 2, 5],
      ],
      [4, 9, 13],
      [1, 1, 1, 1],
      "equality"
    )
  },
  {
    name: "Klee-Minty cube  D=4",
    program: Program.fromArrays(
      [
        [1, 0, 0, 0],
        [4, 1, 0, 0],
        [8, 4, 1, 0],
        [16, 8, 4, 1],
      ],
      [5, 25, 125, 625],
      [8, 4, 2, 1],
      "primal",
    )
  }
]

export default examples;