// import Fraction from 'fraction.js/bigfraction'
import Fraction from 'fraction.js'

export const zero     = new Fraction("0");
export const one      = new Fraction("1");
export const minusOne = new Fraction("-1");

export const isInteger = fraction => fraction.divisible(one);
export const isIntegral = arr => arr.every(v => isInteger(v));
export const isIntegral2D = arr => arr.every(r => isIntegral(r));
export const isNonNegative = arr => arr.every(v => v.compare(zero) >= 0);

export const range = (n, st=0) => [...Array(n).keys()].map(i => i + st);

export const copyArr = arr => [...arr];
export const copyArr2D = arr => arr.map(r => copyArr(r));

export const addArrays  = (a, b) => a.map((v, i) => v.add(b[i]));
export const scaleArray = (v, a) => v.map(v => v.mul(a));

export const last = arr => arr[arr.length - 1];

export const countTrue = arr => arr.reduce((count, v) => v ? count + 1 : count, 0);
export const truePrefixSum = arr => arr.reduce(
                                    ([sum, prefSum], v) => {
                                      sum = v ? sum + 1 : sum;
                                      prefSum.push(sum);
                                      return [sum, prefSum];
                                    }, [0, []]);

export const listToBoolArray = (arr, size) => arr.reduce(
                                              (res, v) => {
                                                if (v >= 0) res[v] = true; return res;
                                              }, range(size).map(() => false));

export const inverseArray = (arr, size) => arr.reduce(
                                            (res, v, i) => {
                                              if (v >= 0) res[v] = i; return res;
                                            }, range(size).map(() => -1));

export const cutToLength = (arr, len, getDefault) => {
  if (arr.length >= len) {
      return arr.slice(0, len);
  }
  return copyArr(arr).concat(
    range(len - arr.length).map(i => getDefault()));
}

export const updateArr2D = (arr, i, j, val) => {
  const newArr = copyArr2D(arr);
  newArr[i][j] = val;
  return newArr;
}

export const updateArr = (arr, i, val) => {
  const newArr = copyArr(arr);
  newArr[i] = val;
  return newArr;
}

export const frMod = (a, b) => a.mod(b).add(b).mod(b);
export const floor = a => a.sub(frMod(a, one));
export const ceil  = a => a.add(frMod(one.sub(frMod(a, one)), one));

const maxFactor = (f, val) => {
  let exp = 0;
  let fct = 1;
  while (val % f === 0) {
    fct = fct * f;
    val /= f;
    exp ++;
  } 
  return [exp, fct];
}

export const decimalFraction = val => {
  const den = val.d;
  let [e2, f2] = maxFactor(2, den);
  let [e5, f5] = maxFactor(5, den);
  if (den / (f2 * f5) !== 1) return null;
  let res = val.n;
  while (e2 < e5) { e2 ++; res *= 2; }
  while (e5 < e2) { e5 ++; res *= 5; }
  let rep = res.toString().padStart(e2 + 1, '0');
  return (val.s === -1 ? "-" : "") + (e2 === 0 ? rep : `${rep.slice(0, -e2)}.${rep.slice(-e2)}`);
}

export const toLatex = (val, excludeWhole=false, useDecimal=true) => {
  if (useDecimal) {
    const rep = decimalFraction(val);
    if (rep !== null) return rep;
  }
  return val.toLatex(excludeWhole);
}

export function isAllowedToSelect(i, j, selection) {
  switch (selection.type) {
    case "rows":
      return selection.allowed[i];
    case "columns":
    case "base_columns":
      return selection.allowed[j];
    case "cells":
      return selection.allowed[i][j];
    default:
      return false;
  }
}

export function isInSelection(i, j, selection) {
  switch (selection.type) {
    case "rows":
      return selection.selected[i];
    case "columns":
    case "base_columns":
      return selection.selected[j];
    case "cells":
      return selection.selected[i][j];
    default:
      return false;
  }
}

export function isSecondarySelected(i, j, selection, state) {
  return selection.type === 'base_columns' && i > 0 && selection.selected[state.baseVariables[i-1]];
}

export function setSelection(i, j, selection, value) {
  switch (selection.type) {
    case "rows":
      return {...selection, selected: updateArr(selection.selected, i, value)};
    case "columns":
    case "base_columns":
      return {...selection, selected: updateArr(selection.selected, j, value)};
    case "cells":
      return {...selection, selected: updateArr2D(selection.selected, i, j, value)};
    default:
      return {...selection};
  }
}

export function flipSelection(i, j, selection) {
  return setSelection(i, j, selection, !isInSelection(i, j, selection));
}