import './App.scss';
import { useState } from 'react';
import { ProgramInput } from './Input'
import { InteractiveTableau } from './InteractiveTableau'

function App() {
  const [initialProgram, setInitialProgram] = useState(null);
  if (initialProgram === null) {
    return (
      <div id="wrapper">
        <ProgramInput 
          onFinish={(program) => setInitialProgram(program)}
        />
      </div>
    );
  } else {
    return (
      <div id="wrapper">
        <InteractiveTableau
          initialProgram={initialProgram}
          onStartOver={() => setInitialProgram(null)}
        />
      </div>
    );
  }
}

export default App;
