import { copyArr, copyArr2D, range, zero, one, minusOne, floor, ceil, 
  last, isInteger, isIntegral, isNonNegative, truePrefixSum, listToBoolArray, inverseArray} from './utils';
import Program from './Program';
import { pivot, calculatePrimalPivots, calculateDualPivots, nonePivotSuggestion, emptyDualPivots, emptyPrimalPivots } from './pivots'

export function getStateFromProgram(program) {
  if (program.form === 'equality' || program.form === 'equality_dual')
    return EqualityState.getFromProgram(program);
  else
    return BasicState.getFromProgram(program);
}

function validationFunction(allowed, selectedPart, allowedErrorMessage) {
  return args => {
    if (!Array.isArray(args) || !args.every(e => typeof e === 'boolean')) 
      return [false, "Argument must be an array of booleans"];
    if (!args.every((v, i) => !v || allowed[i]))
      return [false, allowedErrorMessage];
    if (!args.includes(true))
      return [false, `At least one ${selectedPart} must be selected.`];
    return [true, null];
  };
}

function primalGomoryCutTransition(state, func) {
  const allowed = range(state.m + 1).map(i => !isInteger(last(state.tableau[i])));
  return {
    label: "Add Gomory cuts for primal LP",
    args: "rows",
    prompt: "Select rows to use in making cuts",
    allowed: allowed,
    defaultSelection: range(state.m + 1).map(() => false),
    validateArgs: validationFunction(allowed, "row", "Only rows with non integer value in the last column can be used."),
    getNewState: func
  };
}

function dualGomoryCutTransition(state, func) {
  const allowed = range(state.columns).map(i => !isInteger(state.tableau[0][i]));
  return {
    label: "Add Gomory cuts for dual LP",
    args: "columns",
    prompt: "Select columns to use in making cuts",
    allowed: allowed,
    defaultSelection: range(state.columns).map(() => false),
    validateArgs: validationFunction(allowed, "column", "Only columns with non integer value in the first row can be used."),
    getNewState: func
  }
}

function standardStatusMessage(state, canStart=true, allowPrimalCut=true, allowDualCut=true) {
  if (state.primalUnbounded) {
    return "The primal LP is unbounded.";
  } else if (state.dualUnbounded) {
    return "The dual LP is unbounded.";
  } else if(state.iter === 0 && !state.primalFeasible && canStart) {
    if (state.dualFeasible) {
      return "Use the dual simplex method. Alternatively use an auxiliary LP or Farkas lemma to find a feasible primal solution";
    }
    return "Use an auxiliary LP or Farkas lemma to find a feasible primal solution";
  } else if (state.primalFeasible && state.dualFeasible) {
    let canPrimalCut = !isIntegral(state.primalSolution) && allowPrimalCut;
    let canDualCut = !isIntegral(state.dualSolution) && state.primalProgram.isIntegral() && allowDualCut;
    if (canPrimalCut && canDualCut) {
      return "The LP is optimized, but the primal and dual solutions aren't integral. You can use Gomory cuts to look for integral solutions."
    } else if (canPrimalCut) {
      return "The LP is optimized, but the primal solution isn't integral. You can use Gomory cuts to look for an integral solution."
    } else if (canDualCut) {
      return "The LP is optimized, but the dual solution isn't integral. You can use Gomory cuts to look for an integral solution."
    }
    return "An optimal solution has been reached.";
  } else if (state.primalFeasible) {
    return "Use the primal simplex method to optimize the LP.";
  } else if (state.dualFeasible) {
    return "Use the dual simplex method to optimize the LP.";
  }
  return "";
}

class BasicState {
  constructor({tableau, n, m, primalLabels, dualLabels, baseVariables, primalProgram, iter, columns, addInfo=true}) {
    this.tableau = tableau;
    this.n = n;
    this.m = m;
    this.columns = columns;
    this.primalLabels = primalLabels;
    this.dualLabels = dualLabels;
    this.baseVariables = baseVariables;
    this.primalProgram = primalProgram;
    this.dualProgram = primalProgram.setForm(primalProgram.form === 'primal' ? 'dual' : 'equality_dual').setLabels(this.dualLabels);
    this.iter = iter;

    if (addInfo)
      this.addInformation();
  }

  static getFromProgram(program) {
    let tableau = [
      program.c.map(v => v.neg()).concat(
        range(program.m + 1).map(() => zero))
    ].concat(
      program.A.map((row, i) =>
        row.concat(
          range(program.m).map(v => v === i ? one : zero)
        ).concat(program.b[i])
      )
    );
    
    const primalLabels = range(program.n).map(i => `x_{${i+1}}`)
          .concat(range(program.m).map(i => `s_{${i+1}}`));
    const dualLabels = range(program.m).map(i => `y_{${i+1}}`);
    const baseVariables = range(program.m).map(i => program.n + i);
  
    return new BasicState({
      tableau: tableau,
      n: program.n,
      m: program.m,
      columns: program.n + program.m + 1,
      primalLabels: primalLabels,
      dualLabels: dualLabels,
      baseVariables: baseVariables,
      primalProgram: program.form === "dual" ? program.setForm("primal") : program,
      iter: 0,
    });
  }

  getStatusMessage() {
    return standardStatusMessage(this);
  }

  getPossibleTransitions() {
    if (this.primalUnbounded || this.dualUnbounded) {
      return [];
    } else if(this.iter === 0 && !this.primalFeasible) {
      return [{ 
          label: "Make an auxiliary LP", 
          args: "none",
          getNewState: () => AuxiliaryState.getFromBasicState(this) 
        }, { 
          label: "Use Farkas Lemma", 
          args: "none",
          getNewState: () => FarkasState.getFromBasic(this)
      }];
    } else if (this.primalFeasible && this.dualFeasible) {
      let res = [];
      if (!isIntegral(this.primalSolution)) {
        res.push(primalGomoryCutTransition(this, rows => PrimaryGomory.getFirstCut(this, rows)))
      }
      if (!isIntegral(this.dualSolution) && this.primalProgram.isIntegral()) {
        res.push(dualGomoryCutTransition(this, cols => DualGomory.getFirstCut(this, cols)));
      }
      return res;
    }
    return [];
  }

  addInformation() {  
    this.isBase = listToBoolArray(this.baseVariables, this.columns - 1);
    
    this.calculateSolutions();
    this.calculatePivots();
    this.calculatePrograms();
    this.calculateDisabled();

    this.transitions = this.getPossibleTransitions();
  }

  calculatePivots() {
    const [primalPivots, primalUnbounded] = calculatePrimalPivots(this.tableau, this.isBase);
    const [dualPivots  , dualUnbounded ] = calculateDualPivots(this.tableau);

    this.suggestedPivots = {
      "none": nonePivotSuggestion(this.tableau),
      ...primalPivots,
      ...dualPivots,
    }
    this.defaultSuggestion = "bland_primal";
    this.primalUnbounded = primalUnbounded;
    this.dualUnbounded  = dualUnbounded;
  }

  calculateSolutions() {
    this.primalFeasible = isNonNegative(this.tableau.slice(1).map(r => last(r)));
    this.dualFeasible   = isNonNegative(this.tableau[0].slice(0, -1));

    this.primalSolution = range(this.columns - 1).map(() => zero);
    for (let i = 0; i < this.m; i ++) {
      this.primalSolution[this.baseVariables[i]] = last(this.tableau[i+1]);
    }

    if (!this.primalProgram.equalityForm) {
      this.dualSolution = Array(this.m);
      for (let i = 0; i < this.m; i ++) {
        this.dualSolution[i] = this.tableau[0][i + this.n];
      }
    }
  }

  calculatePrograms() {
    this.programs = [{
      name: "Primal LP",
      program: this.primalProgram,
    }, {
      name: "Dual LP",
      program: this.dualProgram,
    }];
    this.defaultProgram = "Primal LP";
  }

  calculateDisabled() {
    this.disabledColumns = range(this.columns).map(() => false);
    this.disabledRows = this.tableau.map(() => false);
  }

  canPivot(i, j) {
    return !(j === this.columns - 1 || i === 0  ||
            this.tableau[i][j].equals(zero) ||
            this.isBase[j]);
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new BasicState({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class FixBaseBasic extends BasicState {
  constructor(args) {
    super({...args, addInfo: false});
    this.baseFixed = this.baseVariables.every(v => this.tableau[0][v].equals(zero));
    this.addInformation();
  }

  getStatusMessage() {
    if (this.baseFixed)
      return "The top row is fixed."
    return "Make pivots to represent the top row in the new base."
  }

  canPivot(i, j) {
    return this.isBase[j] && !this.tableau[0][j].equals(zero) && this.tableau[i][j].equals(one);
  }

  getPossibleTransitions() {
    if (this.baseFixed) {
      return [{
        label: "Continue", 
        args: "none",
        getNewState: () => this.backToBasic()
      }];
    }
    return [];
  }

  calculatePivots() {
    const availablePivots = this.tableau.map((row, i) => row.map((v, j) => this.canPivot(i, j)));
    this.suggestedPivots = {
      "none": { pivots: availablePivots, description: "None"},
      "generic_dual": { pivots: availablePivots, description: "Generic pivot for dual simplex"},
      "bland_dual": { pivots: availablePivots, description: "Bland's rule for dual simplex"},
      "generic_primal": { pivots: availablePivots, description: "Generic pivot for primal simplex"},
      "bland_primal": { pivots: availablePivots, description: "Bland's rule for primal simplex"},
    };
    this.defaultSuggestion = "bland_primal";
    this.primalUnbounded = false;
    this.dualUnbounded = false;
  }

  backToBasic() {
    return new BasicState({...this});
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new FixBaseBasic({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    });
  }
}

class PrimaryGomory extends BasicState {
  constructor({addedOverall, initialN, initialM, ...args}) {
    super({...args, addInfo: false});
    this.addedOverall = addedOverall;
    this.initialN = initialN;
    this.initialM = initialM;
    this.addInformation();
  }

  static getFirstCut(state, rows) {
    return (new PrimaryGomory({
      ...state,
      addedOverall: 0,
      initialN: state.n,
      initialM: state.m,
    })).addGomoryCuts(rows);
  }

  getStatusMessage() {
    return standardStatusMessage(this, false, true, false);
  }

  getPossibleTransitions() {
    if (this.primalUnbounded || this.dualUnbounded) {
      return [];
    } else if (this.primalFeasible && this.dualFeasible) {
      let res = [];
      if (!isIntegral(this.primalSolution)) {
        res.push(primalGomoryCutTransition(this, rows => this.addGomoryCuts(rows)))
        const possibleToRemove = range(this.columns).map(j => (j >= this.initialN + this.initialM && j < this.columns - 1) && this.isBase[j]);
        if (possibleToRemove.some(v => v)) {
          res.push({
            label: "Remove superfluous cuts",
            args: "base_columns",
            prompt: "Select columns to remove",
            allowed: possibleToRemove,
            defaultSelection: range(this.columns).map(() => false),
            validateArgs: validationFunction(possibleToRemove, "column", "Only columns added with Gomory cuts that are currently in the base can be removed."),
            getNewState: cols => this.removeExtraColumns(cols)
          })
        }
      }
      return res;
    }
    return [];
  }

  addGomoryCuts(rows) {
    let newTableau = copyArr2D(this.tableau);
    let newBaseVariables = copyArr(this.baseVariables);
    let rowsUsed = 0;
    for (let i = 0; i <= this.m; i ++) {
      if (!rows[i]) continue;
      const col = this.tableau[0].length + rowsUsed;
      const tab = newTableau;
      const newRow = range(col + 1).map(j => 
        j === col     ? tab[i][col-1].sub(floor(tab[i][col-1])).neg() :
        j === col - 1 ? one : 
        tab[i][j].sub(floor(tab[i][j])).neg());
      newTableau = tab.map(row => row.slice(0, col-1).concat(zero, row[col-1]))
                      .concat([newRow]);
      newBaseVariables.push(col - 1);                      
      rowsUsed ++;
    }
    const newPrimalLabels = this.primalLabels.concat(range(rowsUsed).map(i => `s_{${this.initialM + this.addedOverall + i + 1}}`));
    const newDualLabels = this.dualLabels.concat(range(rowsUsed).map(i => `y_{${this.initialM + this.addedOverall + i + 1}}`));
    const newPrimalProgram = Program.fromTableau(this.n, this.m + rowsUsed, newTableau, newBaseVariables, newPrimalLabels)

    return new PrimaryGomory({
      ...this,
      m: this.m + rowsUsed,
      columns: this.columns + rowsUsed,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      dualLabels: newDualLabels,
      primalProgram: newPrimalProgram,
      iter: this.iter + 1,
      addedOverall: this.addedOverall + rowsUsed,
    });
  }

  removeExtraColumns(cols) {
    const [removedColumns, prefSum] = truePrefixSum(cols);
    const newTableau = this.tableau.filter((r, i) => i === 0 ? true : !cols[this.baseVariables[i-1]]).map(row => row.filter((v, j) => !cols[j]));
    const newBaseVariables = this.baseVariables.filter(v => !cols[v]).map(v => v - prefSum[v]);
    const newPrimalLabels = this.primalLabels.filter((l, i) => !cols[i]);
    const newDualLabels = this.dualLabels.filter((l, i) => !cols[i + this.n]);
    const newPrimalProgram = Program.fromTableau(this.n, this.m - removedColumns, newTableau, newBaseVariables, newPrimalLabels)
    return new PrimaryGomory({
      ...this,
      m: this.m - removedColumns,
      columns: this.columns - removedColumns,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      dualLabels: newDualLabels,
      primalProgram: newPrimalProgram,
    })
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new PrimaryGomory({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class DualGomory extends BasicState {
  constructor({initialN, addedOverall, ...args}) {
    super({...args, addInfo: false});
    this.initialN = initialN;
    this.addedOverall = addedOverall;

    this.addInformation();
  }

  static getFirstCut(state, cols) {
    return (new DualGomory({
      ...state,
      initialN: state.n,
      addedOverall: 0,
    })).addGomoryCuts(cols);
  }

  getStatusMessage() {
    return standardStatusMessage(this, false, false, true);
  }

  getPossibleTransitions() {
    if (this.primalUnbounded || this.dualUnbounded) {
      return [];
    } else if (this.primalFeasible && this.dualFeasible) {
      let res = [];

      if (!isIntegral(this.dualSolution)) {
        res.push(dualGomoryCutTransition(this, cols => this.addGomoryCuts(cols)));

        const possibleToRemove = range(this.columns).map(i => i >= this.initialN && i < this.n && !this.isBase[i]);
        if (possibleToRemove.some(v => v)) {
          res.push({
            label: "Remove superfluous cuts",
            args: "columns",
            prompt: "Select columns to remove",
            allowed: possibleToRemove,
            defaultSelection: range(this.columns).map(() => false),
            validateArgs: validationFunction(possibleToRemove, "column", "Only columns added with Gomory cuts that are not currently in the base can be removed."),
            getNewState: cols => this.removeExtraColumns(cols)
          })
        }
      }
      return res;
    }
    return [];
  }

  addGomoryCuts(cols) {
    let addedRows = range(this.m + 1).map(() => []);
    let columnsAdded = 0;
    for (let j = 0; j <= this.columns; j ++) {
      if (!cols[j]) continue;
      addedRows = addedRows.map((row, i) =>
        i === 0 ? 
        row.concat(this.tableau[i][j].sub( ceil(this.tableau[i][j]))) :
        row.concat(this.tableau[i][j].sub(floor(this.tableau[i][j])))
      );
      columnsAdded ++;
    }
    const newTableau = this.tableau.map((row, i) => row.slice(0, this.n).concat(addedRows[i]).concat(row.slice(this.n)));
    const newBaseVariables = this.baseVariables.map(v => v >= this.n ? v + columnsAdded : v);
    const newPrimalLabels = this.primalLabels.slice(0, this.n)
                .concat(range(columnsAdded).map(i => `x_{${this.initialN + this.addedOverall + i + 1}}`))
                .concat(this.primalLabels.slice(this.n));
    const newPrimalProgram = Program.fromTableau(this.n + columnsAdded, this.m, newTableau, newBaseVariables, newPrimalLabels);
    return new DualGomory({
      ...this,
      n: this.n + columnsAdded,
      columns: this.columns + columnsAdded,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      primalProgram: newPrimalProgram,
      addedOverall: this.addedOverall + columnsAdded,
    });
  }

  removeExtraColumns(cols) {
    const [removedColumns, prefSum] = truePrefixSum(cols);
    const newTableau = this.tableau.map(row => row.filter((v, j) => !cols[j]));
    const newBaseVariables = this.baseVariables.map(v => v - prefSum[v]);
    const newPrimalLabels = this.primalLabels.filter((l, i) => !cols[i]);
    const newPrimalProgram = Program.fromTableau(this.n - removedColumns, this.m, newTableau, newBaseVariables, newPrimalLabels);
    return new DualGomory({
      ...this,
      n: this.n - removedColumns,
      columns: this.columns - removedColumns,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      primalProgram: newPrimalProgram,
    })
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new DualGomory({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class AuxiliaryState extends BasicState {
  constructor({auxiliaryProgram, savedObjective, ...args}) {
    super({...args, addInfo: false});
    this.auxiliaryProgram = auxiliaryProgram;
    this.savedObjective = savedObjective;
    this.addInformation();
  }

  static getFromBasicState(state) {
    return new AuxiliaryState({
      ...state,
      auxiliaryProgram: Program.fromArrays(
        state.primalProgram.A.map(r => Array.of(minusOne).concat(r)),
        state.primalProgram.b,
        Array.of(minusOne).concat(range(state.primalProgram.c.length).map(() => zero)),
        "primal",
        Array.of("x_{0}").concat(state.primalLabels.slice(0, state.n)),
      ),
      tableau: state.tableau.map((row, i) => i === 0 ? 
        range(state.columns + 1).map(i => i === 0 ? one : zero) : 
        Array.of(minusOne).concat(row)),
      n: state.n + 1,
      columns: state.columns + 1,
      primalLabels: Array.of("x_{0}").concat(state.primalLabels),
      baseVariables: state.baseVariables.map(x => x + 1),
      savedObjective: state.tableau[0],
      iter: 0,
    });
  }

  getStatusMessage() {
    if (this.primalFeasible && this.dualFeasible) {
      if (this.primalSolution[0].equals(zero)) {
        return 'The auxiliary LP is optimized and $x_0$ is equals to 0, so we can return to the original LP';
      } else {
        return 'The auxiliary LP is optimized, but $x_0$ is not equal to 0, so the original LP is infeasible';
      }
    } else if (this.iter === 0) {
      if (this.dualFeasible)
        return "The primal solution is not feasible, pivot with $x_0$ as the entering variable and as the leaving the one with the lowest value in the current basic solution. Alternatively use the dual simplex algorithm.";
      return 'The primal solution is not feasible, pivot with $x_0$ as the entering variable and as the leaving the one with the lowest value in the current basic solution.';
    } else {
      return 'Optimize the auxiliary LP to find a feasible basic solution to the initial LP or determine that it is infeasible';
    }
  }

  getFirstPivotRow() {
    let correctI = -1;
    let minB = zero;
    for (let k = 1; k <= this.m; k ++) {
      if (last(this.tableau[k]).compare(minB) < 0) {
        minB = last(this.tableau[k]);
        correctI = k;
      }
    }
    return correctI;
  }

  calculatePivots() {
    const [primalPivots, primalUnbounded] = calculatePrimalPivots(this.tableau, this.isBase);
    const [dualPivots  , dualUnbounded ] = calculateDualPivots(this.tableau);

    this.suggestedPivots = {
      "none": nonePivotSuggestion(this.tableau),
      ...primalPivots,
      ...dualPivots,
    }

    if (this.iter === 0) {
      const row = this.getFirstPivotRow();
      const auxiliaryPivot = this.tableau.map((r, i) => r.map((v, j) => (i === row && j === 0)));

      this.suggestedPivots = {
        ...this.suggestedPivots,
        "generic_primal": { pivots: auxiliaryPivot, description: "Generic pivot for primal simplex"},
        "bland_primal": { pivots: auxiliaryPivot, description: "Bland's rule for primal simplex"},
      };
    }
    this.defaultSuggestion = "bland_primal";
    this.primalUnbounded = primalUnbounded;
    this.dualUnbounded  = dualUnbounded;
  }

  getPossibleTransitions() {
    if (this.primalFeasible && this.dualFeasible && this.primalSolution[0].equals(zero)) {
      return [{ 
        label: "Return to initial LP", 
        args: "none",
        getNewState: () => this.goBackToBasic() 
      }];
    }
    return [];
  }

  goBackToBasic() {
    const newTableau = this.tableau.map(r => r.slice(1));
    newTableau[0] = this.savedObjective;
  
    return new FixBaseBasic({
      ...this,
      tableau: newTableau,
      n: this.n - 1,
      columns: this.columns - 1,
      primalLabels: this.primalLabels.slice(1),
      baseVariables: this.baseVariables.map(x => x - 1),
      iter: this.outerIter + 1,
    });
  }

  calculatePrograms() {
    if (this.auxiliaryProgram === undefined) return;
    this.programs = [{
      name: "Auxiliary LP",
      program: this.auxiliaryProgram,
    },{
      name: "Dual auxiliary LP",
      program: this.auxiliaryProgram.setForm('dual'),
    }];
    this.defaultProgram = "Auxiliary LP";
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new AuxiliaryState({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class FarkasState extends BasicState {
  constructor({...args}) {
    super(args);
  }
  static getFromBasic(state) {
    return new FarkasState({
      ...state,
      primalProgram: new Program({
        ...state.primalProgram,
        c: state.primalProgram.c.map(() => zero)
      })
    });
  }

  getStatusMessage() {
    if (this.primalFeasible) {
      return "A feasible primal solution has been found.";
    } else if (this.dualUnbounded) {
      return "The initial LP is primal infeasible.";
    } else {
      return "Use dual simplex method to find a feasible primal solution.";
    }
  }

  calculatePivots() {
    const dummyTableau = this.tableau.map((row, i) =>
       i === 0 ? range(this.columns).map(() => zero) : row);
    const [primalPivots, primalUnbounded] = calculatePrimalPivots(dummyTableau, this.isBase);
    const [dualPivots  , dualUnbounded ] = calculateDualPivots(dummyTableau);

    this.suggestedPivots = {
      "none": nonePivotSuggestion(this.tableau),
      ...primalPivots,
      ...dualPivots,
    }
    this.primalUnbounded = primalUnbounded;
    this.dualUnbounded  = dualUnbounded;
  }

  getPossibleTransitions() {
    const thisRef = this;
    if (this.primalFeasible) {
      return [
      { 
        label: "Go back to the initial LP", 
        args: "none",
        getNewState: () => new BasicState(thisRef) 
      }];
    }
    return [];
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new FarkasState({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class EqualityState extends BasicState {
  constructor(args) {
    super(args);
    this.calculateDisabled();
    this.hasBase = this.baseVariables.every((v, i) => v !== -1 || this.disabledRows[i + 1]);
    this.infeasible = range(this.m, 1).some(i => range(this.n).every(j => this.tableau[i][j].compare(zero) < 0 || j === this.baseVariables[i-1]) 
                                            && !last(this.tableau[i]).equals(zero) && !this.disabledRows[i]);
    this.addInformation();
  }

  static getFromProgram(program) {
    let tableau = [
      program.c.map(v => v.neg()).concat(
        range(program.m + 1).map(() => zero))
    ].concat(
      program.A.map((row, i) =>
        row.concat(
          range(program.m).map(v => v === i ? (program.b[i].compare(zero) < 0 ? minusOne : one) : zero)
        ).concat(program.b[i])
      )
    );
    
    const primalLabels = range(program.n).map(i => `x_{${i+1}}`)
          .concat(range(program.m).map(i => `s_{${i+1}}`));
    const dualLabels = range(program.m).map(i => `y_{${i+1}}`);
    const baseVariables = range(program.m).map(() => -1);
  
    return new EqualityState({
      tableau: tableau,
      n: program.n,
      m: program.m,
      columns: program.n + program.m + 1,
      primalLabels: primalLabels,
      dualLabels: dualLabels,
      baseVariables: baseVariables,
      primalProgram: program.setForm('equality'),
      iter: 0,
    });
  }

  calculatePivots() {
    const nonePivots = nonePivotSuggestion(this.tableau);
    if (!this.hasBase || this.infeasible) {
      this.suggestedPivots = {
        "none": nonePivots,
        ...emptyDualPivots(this.tableau),
        ...emptyPrimalPivots(this.tableau),
      }
      this.defaultSuggestion = "none";
      this.primalUnbounded = false;
      this.dualUnbounded  = false;
    } else {
      const [primalPivots, primalUnbounded] = calculatePrimalPivots(this.tableau, this.isBase, true, this.n);
      const [dualPivots  , dualUnbounded ] = calculateDualPivots(this.tableau, true, this.n);

      this.suggestedPivots = {
        "none": nonePivots,
        ...primalPivots,
        ...dualPivots,
      }
      this.defaultSuggestion = "bland_primal";
      this.primalUnbounded = primalUnbounded;
      this.dualUnbounded  = dualUnbounded;
    }
  }

  getStatusMessage() {
    if (this.infeasible) {
      return "The primal LP is infeasible."
    } else if (!this.hasBase) {
      if (this.iter === 0)
        return "Find a base by making pivots or following an algorithm."
      return "Find a base by making pivots."
    } else {
      return standardStatusMessage(this, false, false, true);
    }
  }

  calculateSolutions() {
    this.primalFeasible = isNonNegative(this.tableau.slice(1).map(r => last(r)));
    this.dualFeasible   = isNonNegative(this.tableau[0].slice(0, this.n));

    this.primalSolution = range(this.n).map(() => zero);
    for (let i = 0; i < this.m; i ++) {
      if (this.baseVariables[i] !== -1 && this.baseVariables[i] < this.n)
        this.primalSolution[this.baseVariables[i]] = last(this.tableau[i+1]);
    }

    this.dualSolution = Array(this.m);
    for (let i = 0; i < this.m; i ++) {
      this.dualSolution[i] = this.tableau[0][i + this.n];
    }
  }

  getPossibleTransitions() {
    if (!this.hasBase) {
      if (this.iter === 0)
        return [{ 
          label: "Find a base", 
          args: "none",
          getNewState: () => EqualityFirstStage.getFromEqualityState(this) 
        }];
    } else if (this.primalFeasible && this.dualFeasible) {
      if (!isIntegral(this.dualSolution) && this.primalProgram.isIntegral()) {
        return [dualGomoryCutTransition(this, cols => EqualityDualGomory.getFirstCut(this, cols))];
      }
    }
    return [];
  }

  calculateDisabled() {
    this.disabledColumns = range(this.columns).map(i => i >= this.n && i < this.columns - 1);
    this.disabledRows = range(this.m + 1).map(i => i > 0 && range(this.n).every(j => this.tableau[i][j].equals(zero)) && last(this.tableau[i]).equals(zero));
  }

  canPivot(i, j) {
    return !(j >= this.n || i === 0  ||
            this.tableau[i][j].equals(zero) ||
            this.isBase[j]);
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new EqualityState({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    })
  }
}

class EqualityFirstStage extends EqualityState {
  constructor({savedObjective, ...args}) {
    super({...args, addInfo: false});
    this.savedObjective = savedObjective;
    
    if (this.baseVariables.some(v => v === -1)) {
      this.stage = 'base';
    } else {
      if (this.primalFeasible && this.dualFeasible) {
        const invBaseVariables = inverseArray(this.baseVariables, this.n + this.m);
        if (last(this.tableau[0]).compare(zero) < 0) {
          this.stage = 'infeasible';
        } else if (range(this.m, this.n).some(j => this.isBase[j] && 
            range(this.n).some(i => !this.tableau[invBaseVariables[j]+1][i].equals(zero)))) {
          this.stage = 'rebase';
        } else {
          this.stage = 'done';
        }
      } else {
        this.stage = 'optimize';
      }
    }

    this.addInformation();
  }


  static getFromEqualityState(state) {
    return new EqualityFirstStage({
      ...state,
      tableau: state.tableau.map((row, i) => i === 0 ? 
          row.map((v, j) => (j < state.n || j === state.columns - 1) ? zero : one) : row),
      savedObjective: state.tableau[0],
    });
  }

  getStatusMessage() {
    if (this.stage === 'base') {
      return "Make pivots with slack variables to establish a base."
    } else if (this.stage === 'done') {
      return "The program is optimized. We can return to the initial program."
    } else if (this.stage === 'optimize') {
      return "Optimize the LP using the simplex method."
    } else if (this.stage === 'rebase') {
      return "Remove the slack variables from base where possible."
    } else if (this.stage === 'infeasible') {
      return "The initial LP is infeasible."
    }
    return "";
  }

  getPossibleTransitions() {
    if (this.stage === 'done') {
      if (last(this.tableau[0]).compare(zero) === 0)
        return [{
          label: "Return to initial LP", 
          args: "none",
          getNewState: () => this.goBack() 
        }];
    }
    return [];
  }

  goBack() {
    const newTableau = this.tableau.map((row, i) => i === 0 ? this.savedObjective : row);

    return new FixBaseEquality({
      ...this,
      tableau: newTableau,
    });
  }

  calculatePivots() {
    if (this.stage !== 'optimize') {
      this.suggestedPivots = {
        "none": nonePivotSuggestion(this.tableau),
        ...emptyDualPivots(this.tableau),
        ...emptyPrimalPivots(this.tableau),
      }
      this.defaultSuggestion = "none";
      this.primalUnbounded = false;
      this.dualUnbounded  = false;
    } else {
      const [primalPivots, primalUnbounded] = calculatePrimalPivots(this.tableau, this.isBase);
      const [dualPivots  , dualUnbounded ] = calculateDualPivots(this.tableau);

      this.suggestedPivots = {
        "none": nonePivotSuggestion(this.tableau),
        ...primalPivots,
        ...dualPivots,
      }
      this.defaultSuggestion = "bland_primal";
      this.primalUnbounded = primalUnbounded;
      this.dualUnbounded  = dualUnbounded;
    }
  }

  calculateDisabled() {
    this.disabledColumns = range(this.columns).map(() => false);
    this.disabledRows = range(this.m + 1).map(() => false);
  }

  canPivot(i, j) {
    if (this.stage === 'base')
      return (this.baseVariables[i-1] === -1) && (i === j - this.n + 1) && !this.isBase[j];
    if (this.stage === 'optimize')
      return !(j === this.columns - 1 || i === 0  ||
        this.tableau[i][j].equals(zero) ||
        this.isBase[j]);
    if (this.stage === 'rebase') 
      return !(j === this.columns - 1 || i === 0  ||
        this.tableau[i][j].equals(zero) ||
        this.isBase[j]) && this.baseVariables[i-1] >= this.n && j < this.n;
    return false;
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new EqualityFirstStage({
      ...this,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      iter: this.iter + 1,
    })
  }
}

class FixBaseEquality extends EqualityState {
  constructor(args) {
    super({...args, addInfo: false});
    this.baseFixed = this.baseVariables.every(v => this.tableau[0][v].equals(zero));
    this.addInformation();
  }

  getStatusMessage() {
    if (this.baseFixed)
      return "The top row is fixed."
    return "Make pivots to represent the top row in the new base."
  }

  canPivot(i, j) {
    return this.isBase[j] && !this.tableau[0][j].equals(zero) && this.tableau[i][j].equals(one);
  }

  calculatePivots() {
    const availablePivots = this.tableau.map((row, i) => row.map((v, j) => this.canPivot(i, j)));
    this.suggestedPivots = {
      "none": { pivots: availablePivots, description: "None"},
      "generic_dual": { pivots: availablePivots, description: "Generic pivot for dual simplex"},
      "bland_dual": { pivots: availablePivots, description: "Bland's rule for dual simplex"},
      "generic_primal": { pivots: availablePivots, description: "Generic pivot for primal simplex"},
      "bland_primal": { pivots: availablePivots, description: "Bland's rule for primal simplex"},
    };
    this.defaultSuggestion = "bland_primal";
    this.primalUnbounded = false;
    this.dualUnbounded = false;
  }

  getPossibleTransitions() {
    if (this.baseFixed) {
      return [{
        label: "Continue", 
        args: "none",
        getNewState: () => this.backToBasic()
      }];
    }
    return [];
  }

  backToBasic() {
    return new EqualityState({...this});
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new FixBaseEquality({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    });
  }
}

class EqualityDualGomory extends EqualityState {
  constructor({initialN, addedOverall, ...args}) {
    super(args);
    this.initialN = initialN;
    this.addedOverall = addedOverall;

    this.addInformation();
  }

  static getFirstCut(state, cols) {
    return (new EqualityDualGomory({
      ...state,
      initialN: state.n,
      addedOverall: 0,
    })).addGomoryCuts(cols);
  }

  getStatusMessage() {
    return standardStatusMessage(this, false, false, true);
  }

  getPossibleTransitions() {
    if (this.primalUnbounded || this.dualUnbounded) {
      return [];
    } else if (this.primalFeasible && this.dualFeasible) {
      let res = [];

      if (!isIntegral(this.dualSolution)) {
        res.push(dualGomoryCutTransition(this, cols => this.addGomoryCuts(cols)));

        const possibleToRemove = range(this.columns).map(i => i >= this.initialN && i < this.n && !this.isBase[i]);
        if (possibleToRemove.some(v => v)) {
          res.push({
            label: "Remove superfluous cuts",
            args: "columns",
            prompt: "Select columns to remove",
            allowed: possibleToRemove,
            defaultSelection: range(this.columns).map(() => false),
            validateArgs: validationFunction(possibleToRemove, "column", "Only columns added with Gomory cuts that are not currently in the base can be removed."),
            getNewState: cols => this.removeExtraColumns(cols)
          })
        }
      }
      return res;
    }
    return [];
  }

  addGomoryCuts(cols) {
    let addedRows = range(this.m + 1).map(() => []);
    let columnsAdded = 0;
    for (let j = 0; j <= this.columns; j ++) {
      if (!cols[j]) continue;
      addedRows = addedRows.map((row, i) =>
        i === 0 ? 
        row.concat(this.tableau[i][j].sub( ceil(this.tableau[i][j]))) :
        row.concat(this.tableau[i][j].sub(floor(this.tableau[i][j])))
      );
      columnsAdded ++;
    }
    const newTableau = this.tableau.map((row, i) => row.slice(0, this.n).concat(addedRows[i]).concat(row.slice(this.n)));
    const newBaseVariables = this.baseVariables.map(v => v >= this.n ? v + columnsAdded : v);
    const newPrimalLabels = this.primalLabels.slice(0, this.n)
                .concat(range(columnsAdded).map(i => `x_{${this.initialN + this.addedOverall + i + 1}}`))
                .concat(this.primalLabels.slice(this.n));
    const newPrimalProgram = Program.fromTableau(this.n + columnsAdded, this.m, newTableau, newBaseVariables, newPrimalLabels).setForm('equality');
    return new EqualityDualGomory({
      ...this,
      n: this.n + columnsAdded,
      columns: this.columns + columnsAdded,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      primalProgram: newPrimalProgram,
      addedOverall: this.addedOverall + columnsAdded,
    });
  }

  removeExtraColumns(cols) {
    const [removedColumns, prefSum] = truePrefixSum(cols);
    const newTableau = this.tableau.map(row => row.filter((v, j) => !cols[j]));
    const newBaseVariables = this.baseVariables.map(v => v - prefSum[v]);
    const newPrimalLabels = this.primalLabels.filter((l, i) => !cols[i]);
    const newPrimalProgram = Program.fromTableau(this.n - removedColumns, this.m, newTableau, newBaseVariables, newPrimalLabels);
    return new EqualityDualGomory({
      ...this,
      n: this.n - removedColumns,
      columns: this.columns - removedColumns,
      tableau: newTableau,
      baseVariables: newBaseVariables,
      primalLabels: newPrimalLabels,
      primalProgram: newPrimalProgram,
    })
  }

  pivot(i, j) {
    const [newTableau, newBaseVariables] = pivot(this.tableau, this.baseVariables, i, j);
    
    return new EqualityDualGomory({
      ...this,
      baseVariables: newBaseVariables,
      tableau: newTableau,
      iter: this.iter + 1,
    });
  }
}