import { InlineMath } from 'react-katex'
import { range, zero, toLatex, isInSelection, isAllowedToSelect, last, isSecondarySelected } from './utils'
import './Tableau.scss'

function TableauCell({
  value,
  position = "inner",
  hidden = false,
  showInvariant = false, 
  isBase = false,  
  isCorner = false, 
  isHighlighted = false, 
  isDisabled = false,
  isSelected = false,
  isSelectable = false,
  isSecondarySelected = false,
  showPointer = true,
  showSelection = false,
  showDisabled = true,
  useDecimal = true,
  useMixed = false,
  onClick = f => f, 
}) {
  const isInner = position === 'inner';
  const isNonNegative = value.compare(zero) >= 0;
  const classes = [
    "tableau-cell",
    `cell-${position}`,
    showInvariant &&  isNonNegative && !isDisabled ? "invariant-holds"  : "",
    showInvariant && !isNonNegative && !isDisabled ? "invariant-broken" : "",
    showPointer && 
        ((!isDisabled && isInner && !showSelection) ||
        (showSelection && isSelectable))  ? "clickable-cell"      : "",
    hidden                                ? "hidden-cell"         : "",
    isCorner                              ? "corner-cell"         : "",
    isBase && (isInner || !showInvariant) ? "base-cell"           : "",
    isHighlighted && !showSelection       ? "highlighted-cell"    : "",
    showSelection && isSelected           ? "selected-cell"       : "",
    showSelection && !isSelectable        ? "not-selectable-cell" : "",
    showSelection && isSecondarySelected  ? "secondary-cell"      : "",
    showDisabled && !showSelection && 
      isDisabled                          ? "disabled-cell"       : "",
  ].join(' ');
  
  return (
    <td
     className={classes}
     onClick={onClick}
    >
      <InlineMath>{toLatex(value, useMixed, useDecimal)}</InlineMath>
    </td>
  );
}

function cornerCell({state, position, selection, showSelection, onCellClick, miscSettings}) {
  return <TableauCell 
    value={last(state.tableau[0])}
    position={position}
    isSelected={showSelection && isInSelection(0, state.columns - 1, selection)}
    isSelectable={showSelection && isAllowedToSelect(0, state.columns - 1, selection)}
    isSecondarySelected={showSelection && isSecondarySelected(0, state.columns - 1, selection, state)}
    onClick={() => onCellClick(0, state.columns - 1)}
    {...{...miscSettings, showSelection}}
  />;
}

function bCell({state, i, position, selection, showSelection, onCellClick, highlightInvariants, miscSettings}) {
  return <TableauCell 
    value={last(state.tableau[i])}
    position={position}
    showInvariant={highlightInvariants}
    isDisabled={state.disabledRows[i]}
    isSelected={showSelection && isInSelection(i, state.columns - 1, selection)}
    isSelectable={showSelection && isAllowedToSelect(i, state.columns - 1, selection)}
    isSecondarySelected={showSelection && isSecondarySelected(i, state.columns - 1, selection, state)}
    onClick={() => onCellClick(i, state.columns - 1)}
    {...{...miscSettings, showSelection}}
  />;
}

function objectiveRow({state, position, selection, showSelection, showBase, onCellClick, showBColumnsLeft, showSlackLeft, highlightBase, highlightInvariants, miscSettings}) {
  return <tr>
    <td></td>
    {!showBColumnsLeft ? null :
      cornerCell({state, position: `${position}-left`, selection, onCellClick, showSelection, miscSettings})}
    {!showSlackLeft ? null : range(state.m).map(j => 
      <TableauCell 
        key={state.n + j}
        value={state.tableau[0][state.n + j]}
        position={position}
        showInvariant={highlightInvariants}
        hidden={state.isBase[state.n + j] && !state.disabledColumns[state.n + j] && !showBase}
        isDisabled={state.disabledColumns[state.n + j]}
        isBase={state.isBase[state.n + j] && highlightBase}
        isSelected={showSelection && isInSelection(0, state.n + j, selection)}
        isSelectable={showSelection && isAllowedToSelect(0, state.n + j, selection)}
        isSecondarySelected={showSelection && isSecondarySelected(0, state.n + j, selection, state)}
        onClick={() => onCellClick(0, state.n + j)}
        {...{...miscSettings, showSelection}}
      />
    )}
    {range(state.n).map(j => 
      <TableauCell 
        key={j}
        value={state.tableau[0][j]}
        position={position}
        showInvariant={highlightInvariants}
        hidden={state.isBase[j] && !state.disabledColumns[j] && !showBase}
        isDisabled={state.disabledColumns[j]}
        isBase={state.isBase[j] && highlightBase}
        isSelected={showSelection && isInSelection(0, j, selection)}
        isSelectable={showSelection && isAllowedToSelect(0, j, selection)}
        isSecondarySelected={showSelection && isSecondarySelected(0, j, selection, state)}
        onClick={() => onCellClick(0, j)}
        {...{...miscSettings, showSelection}}
      />
    )}
    {showSlackLeft ? null : range(state.m).map(j => 
      <TableauCell 
        key={j}
        value={state.tableau[0][state.n + j]}
        position={position}
        showInvariant={highlightInvariants}
        hidden={state.isBase[state.n + j] && !state.disabledColumns[state.n + j] && !showBase}
        isDisabled={state.disabledColumns[state.n + j]}
        isBase={state.isBase[state.n + j] && highlightBase}
        isSelected={showSelection && isInSelection(0, state.n + j, selection)}
        isSelectable={showSelection && isAllowedToSelect(0, state.n + j, selection)}
        isSecondarySelected={showSelection && isSecondarySelected(0, state.n + j, selection, state)}
        onClick={() => onCellClick(0, state.n + j)}
        {...{...miscSettings, showSelection}}
      />
    )}
    {showBColumnsLeft ? null :
      cornerCell({state, position: `${position}-right`, selection, onCellClick, showSelection, miscSettings})}
  </tr>;
}

function innerCell({state, i, j, highlightedPivots, showBase, highlightBase, selection, showSelection, onCellClick, miscSettings}) {
  return <TableauCell 
    key={j}
    value={state.tableau[i][j]}
    hidden={state.isBase[j] && !state.disabledColumns[j] && !showBase}
    isBase={state.isBase[j] && !state.disabledColumns[j] && highlightBase}
    isHighlighted={state.suggestedPivots[highlightedPivots].pivots[i][j]}
    isSelected={showSelection && isInSelection(i, j, selection)}
    isSelectable={showSelection && isAllowedToSelect(i, j, selection)}
    isSecondarySelected={showSelection && isSecondarySelected(i, j, selection, state)}
    isDisabled={!state.canPivot(i, j)}
    onClick={() => onCellClick(i, j)}
    {...{...miscSettings, showSelection}}
  />;
}

function TableauDisplay({
  state, 
  onCellClick = f => f, 
  highlightedPivots = "none", 
  highlightBase = true,
  highlightInvariants = true,
  useDecimal = true,
  useMixed = false,
  showBase = true,
  selection = null,
  showPointer = true,
  showDisabled = false,
  showObjectiveBottom = false,
  showBColumnsLeft = false,
  showSlackLeft = false,
}) {
  const showSelection = selection !== null;
  const miscSettings = {useDecimal, useMixed, showPointer, showDisabled};
  return (
    <div className="tableau-box">
      <table className="tableau-table">
        <tbody>
          {showObjectiveBottom ? null :
            objectiveRow({state, position: "top", selection, showSelection, showBase, onCellClick, showBColumnsLeft, showSlackLeft, highlightBase, highlightInvariants, miscSettings})
          }
          {range(state.m, 1).map(i => 
            <tr key={i}>
              <td>
                <InlineMath>
                  {i === 0 ? "" : 
                    (state.baseVariables[i-1] < 0
                    || state.disabledColumns[state.baseVariables[i-1]]
                    || state.disabledRows[i]) ? "-"  :
                    state.primalLabels[state.baseVariables[i-1]]}
                </InlineMath>
              </td>
              {!showBColumnsLeft ? null :
                bCell({state, i, position: "left", selection, showSelection, showBase, onCellClick, highlightInvariants, miscSettings})}
              {!showSlackLeft ? null : range(state.m, state.n).map(j => 
                innerCell({state, i, j, highlightedPivots, showBase, highlightBase, selection, showSelection, onCellClick, miscSettings})
              )}
              {range(state.n).map(j => 
                innerCell({state, i, j, highlightedPivots, showBase, highlightBase, selection, showSelection, onCellClick, miscSettings})
              )}
              {showSlackLeft ? null : range(state.m, state.n).map(j => 
                innerCell({state, i, j, highlightedPivots, showBase, highlightBase, selection, showSelection, onCellClick, miscSettings})
              )}
              {showBColumnsLeft ? null :
                bCell({state, i, position: "right", selection, showSelection, showBase, onCellClick, highlightInvariants, miscSettings})}
            </tr>
          )}
          {!showObjectiveBottom ? null :
            objectiveRow({state, position: "bottom", selection, showSelection, showBase, onCellClick, showBColumnsLeft, showSlackLeft, highlightBase, highlightInvariants, miscSettings})}
          <tr>
            <td></td>
            {showBColumnsLeft ? <td></td> : null}
            {!showSlackLeft ? null : range(state.m).map(j => 
              <td key={state.n + j}>
                <InlineMath>
                  {state.primalLabels[state.n + j]}
                </InlineMath>
              </td>
            )}
            {range(state.n).map(j => 
              <td key={j}>
                <InlineMath>
                  {state.primalLabels[j]}
                </InlineMath>
              </td>
            )}
            {showSlackLeft ? null : range(state.m).map(j => 
              <td key={state.n + j}>
                <InlineMath>
                  {state.primalLabels[state.n + j]}
                </InlineMath>
              </td>
            )}
            {!showBColumnsLeft ? <td></td> : null}
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export { TableauDisplay };