import './Layout.scss'

export function Pane({name, label, children}) {
  return (
    <div className="pane">
      {children}
    </div>
  );
}

export function MultiPane({defaultChoice, chosen, setChosen, children}) {
  let choice = chosen;
  const filteredChildren = children.flat().filter(c => c !== null);
  if (!filteredChildren.some(c => c.props.name === chosen)) {
    choice = defaultChoice;
    setChosen(defaultChoice);
  }
  return (
    <div className={"multi-pane"}>
      <div className={"pane-list"}>
        {filteredChildren.map((child, i) =>
          <div
            key={i} 
            className={`pane-button ${choice === child.props.name ? "pane-button-selected" : ""}`}
            onClick={() => setChosen(child.props.name)}
          >
            {child.props.label}
          </div>
        )}
        <div style={{clear: "both"}}></div>
      </div>
      {filteredChildren.map((child, i) =>
        <div key={i} className={`${choice !== child.props.name ? "pane-hidden" : ""}`}>
          {child}
        </div>
      )}
    </div>
  );
}

export function SplitPane({children}) {
  return (
    <div className="split-pane">
      <div className="split">{children[0]}</div>
      <div className="split-border"></div>
      <div className="split">{children[1]}</div>
    </div>
  );
}