// import Fraction from 'fraction.js/bigfraction'
import Fraction from 'fraction.js'
import React, { useState } from 'react';
import { InlineMath } from 'react-katex';
import { getStateFromProgram } from './SimplexState'
import { TableauDisplay } from './TableauDisplay'
import Program from './Program';
import examples from './examples'
import { cutToLength, range, zero, updateArr, updateArr2D } from './utils'
import './Input.scss'
import 'katex/dist/katex.min.css'

const MAX_N = 7;
const MAX_M = 7;

function checkInput(input) {
  const re = /^-?((\d+(\/[1-9]\d*)?)|(\d*\.\d+)|(\d+\.))$/;
  return re.test(input);
}

function InputField({value, onChange, ending}) { 
  return (
    <div className="input-field">
      <input 
        className={checkInput(value) ? "correct-input" : "wrong-input"}
        value={value}
        onChange={e => onChange(e.target.value)}
      />
      <span className="label">
        <InlineMath>
          {ending}
        </InlineMath>
      </span>
    </div>
  );
}

function ExampleLoading({examplesList, selected, onChange}) {
  function update(val) {
    onChange(val, parseInt(val) !== -1 ? examplesList[val].program : null);
  }

  return (
    <div className="input-row">
      <label htmlFor="example">Example:</label>
      <select 
        id="example"
        value={selected}
        onChange={e => update(e.target.value)}
      >
        <option value={-1}>Custom</option>
        {examplesList.map((e, i) =>
          <option
            key={i}
            value={i}
          >{e.name}</option>
        )}
      </select>
    </div>
  );
}

function ProgramInput({onFinish}) {
  const [state, setState] = useState({
    program: Program.zeroProgram(2, 1),
    fieldsA: [["0", "0"]],
    fieldsB: ["0"],
    fieldsC: ["0", "0"],
    fieldN: "2",
    fieldM: "1",
    selectedExample: -1,
  });

  const updateSize = (fn, fm) => {
    let n = parseInt(fn);
    let m = parseInt(fm);
    n = isNaN(n) ? state.program.n : Math.max(Math.min(n, MAX_N), 1);
    m = isNaN(m) ? state.program.m : Math.max(Math.min(m, MAX_M), 1);
    
    setState({
      ...state,
      program: state.program.updateSize(n, m),
      fieldsA: cutToLength(
        state.fieldsA.map(row => cutToLength(row, n, () => "0")), 
        m, () => range(n).map(i => "0")),
      fieldsB: cutToLength(state.fieldsB, m, () => "0"),
      fieldsC: cutToLength(state.fieldsC, n, () => "0"),
      fieldN: fn,
      fieldM: fm,
    });
  }

  const adjustSizeFields = () => {
    let n = parseInt(state.fieldN);
    let m = parseInt(state.fieldM);
    n = isNaN(n) ? state.program.n : Math.max(Math.min(n, MAX_N), 1);
    m = isNaN(m) ? state.program.m : Math.max(Math.min(m, MAX_M), 1);

    updateSize(n.toString(), m.toString());
  }

  const updateA = (i, j, val) => {
    setState({
      ...state, 
      program: state.program.updateA(i, j, checkInput(val) ? new Fraction(val) : zero),
      fieldsA: updateArr2D(state.fieldsA, i, j, val),
      selectedExample: -1
    });
  }
  const updateB = (i, val)    => { 
    setState({
      ...state, 
      program: state.program.updateB(i, checkInput(val) ? new Fraction(val) : zero),
      fieldsB: updateArr(state.fieldsB, i, val),
      selectedExample: -1
    });
  }
  const updateC = (j, val)    => {
    setState({
      ...state, 
      program: state.program.updateC(j, checkInput(val) ? new Fraction(val) : zero),
      fieldsC: updateArr(state.fieldsC, j, val),
      selectedExample: -1
    });
  }

  const setForm = newForm => {
    setState({
      ...state,
      program: state.program.setForm(newForm),
      selectedExample: -1
    })
  }

  function reset() {
    setState({
      ...state,
      program: Program.zeroProgram(2, 1),
      fieldsA: [["0", "0"]],
      fieldsB: ["0"],
      fieldsC: ["0", "0"],
      fieldN: "2",
      fieldM: "1",
      selectedExample: -1
    });
  }

  function selectExample(exampleNum, program) {
    if (program === null) {
      setState({
        ...state,
        selectedExample: exampleNum
      });
    } else {
      setState({
        ...state,
        program: program,
        fieldsA: program.A.map(row => row.map(v => v.toFraction())),
        fieldsB: program.b.map(v => v.toFraction()),
        fieldsC: program.c.map(v => v.toFraction()),
        fieldN: program.n.toString(),
        fieldM: program.m.toString(),
        selectedExample: exampleNum
      });  
    }
  }

  console.log(state);

  const isDual = state.program.isDual();
  const isEquality = state.program.isEquality();

  return (
    <>
    <ExampleLoading 
      examplesList={examples}
      selected={state.selectedExample}
      onChange={selectExample}
    />
    <div className="input-row">
      <label htmlFor="n">{"Variables:"}</label>
      <input 
        type="number"
        id="n"
        min="1"
        max={isDual ? MAX_M : MAX_N}
        value={isDual ? state.fieldM : state.fieldN}
        onChange={isDual ? (e => updateSize(state.fieldN, e.target.value)) :
                           (e => updateSize(e.target.value, state.fieldM))}
        onBlur={adjustSizeFields}
        required
      />
      <label htmlFor="m">{isEquality ? "Equalities" : "Inequalities:"}</label>
      <input 
        type="number"
        id="m"
        min="1"
        max={isDual ? MAX_N : MAX_M}
        value={isDual ? state.fieldN : state.fieldM}
        onChange={isDual ? (e => updateSize(e.target.value, state.fieldM)) :
                           (e => updateSize(state.fieldN, e.target.value))}
        onBlur={adjustSizeFields}
        required
      />
      <label htmlFor="form">Form:</label>
      <select 
        id="form"
        value={state.program.form}
        onChange={e => setForm(e.target.value)}
      >
          <option value="primal">Standard form</option>
          <option value="dual">Dual to standard form</option>
          <option value="equality">Equality form</option>
          <option value="equality_dual">Dual to equality form</option>
      </select>
    </div>
    <div className="input-row">
      <button onClick={() => onFinish(state.program)}>Finish</button>
      <button onClick={reset}>Reset</button>
    </div>
    <div style={{clear: "both"}}></div>
    <table id="input-table"><tbody>
      <tr>
        <td className="min-max"><InlineMath>{isDual ? "min" : "max"}</InlineMath></td>
        {range(isDual ? state.program.m : state.program.n).map(i =>
          <React.Fragment key={i}>
            <td>
              <InputField
                value={isDual ? state.fieldsB[i] : state.fieldsC[i]}
                onChange={isDual ? (val => updateB(i, val)) : (val => updateC(i, val))}
                ending={state.program.labels[i]}
              />
            </td>
            <td>
              <InlineMath>
                {(i !== (isDual ? state.program.m : state.program.n) - 1 ? "+" : "")}
              </InlineMath>
            </td>
          </React.Fragment>
        )}
      </tr>
      {range(isDual ? state.program.n : state.program.m).map(i =>
        <tr key={i}>
          <td></td>
          {range(isDual ? state.program.m : state.program.n).map(j =>
            <React.Fragment key={j}>
              <td key={j}>
                <InputField
                  value={isDual ? state.fieldsA[j][i] : state.fieldsA[i][j]}
                  onChange={val => updateA(isDual ? j : i, isDual ? i : j, val)}
                  ending={state.program.labels[j]}
                />
              </td>
              <td>
                <InlineMath>
                  {(j !== (isDual ? state.program.m : state.program.n) - 1 ? "+" : 
                          (isDual ? "\\geq" : isEquality ? "=" : "\\leq"))}
                </InlineMath>
              </td>
            </React.Fragment>
          )}
          <td>
            <InputField
              key={i}
              value={isDual ? state.fieldsC[i] : state.fieldsB[i]}
              onChange={isDual ? (val => updateC(i, val)) : (val => updateB(i, val))}
              ending=""
            />
          </td>
        </tr>
      )}
      {(isEquality && isDual) ? <tr><td></td></tr> :
        <tr>
          <td 
            colSpan={2 * (isDual ? state.program.m : state.program.n)}
            className="variable-list"
          >
            <InlineMath>{state.program.labels.join(',')}</InlineMath>
          </td>
          <td><InlineMath>\geq</InlineMath></td>
          <td><InlineMath>0</InlineMath></td>
        </tr>}
    </tbody></table>
    <TableauDisplay
      state={getStateFromProgram(state.program)}
      showPointer={false}
    />
    </>
  );
}


export { ProgramInput };